import * as React from 'react'

import Helmet from 'react-helmet'

import { graphql, useStaticQuery } from 'gatsby'

import clsx from 'clsx'

import CssBaseline from '@material-ui/core/CssBaseline'
import Drawer from '@material-ui/core/Drawer'

import List from '@material-ui/core/List'
import Typography from '@material-ui/core/Typography'
import Divider from '@material-ui/core/Divider'
import IconButton from '@material-ui/core/IconButton'

//  import Link from '@material-ui/core/Link';
import MenuIcon from '@material-ui/icons/Menu'

import ListItem from '@material-ui/core/ListItem'

import ListItemText from '@material-ui/core/ListItemText'

import { Link, navigate } from 'gatsby'

import Hidden from '@material-ui/core/Hidden'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import AppBar from '@material-ui/core/AppBar'
import { makeStyles, useTheme, Theme, createStyles } from '@material-ui/core/styles'
import { Toolbar } from '@material-ui/core'

const mainQuery = graphql`
  query mainQuery {
    allMarkdownRemark {
      edges {
        node {
          excerpt(truncate: true, pruneLength: 200)
          frontmatter {
            title
            path
          }
          id
        }
      }
    }
    site {
      siteMetadata {
        title
        description
      }
    }
  }
`

const drawerWidth = 300

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex'
  },
  mainLogo: {
    padding: '0 2rem',
    textDecoration: 'none',
    marginTop: '1.75rem',
    marginBottom: '1.75rem',
    fontSize: '2rem',
    lineHeight: '2rem',
    color: '#212529',
    display: 'block',
    position: 'relative'
  },
  toolbar: {
    paddingRight: 24 // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar
  },
  appBar: {
    // zIndex: theme.zIndex.drawer + 1,
    backgroundColor: '#fff',
    color: '#000'
  },
  appBarHeader: {
    textAlign: 'center',
    fontSize: '33px',
    padding: '10px'
  },

  menuButtonHidden: {
    display: 'none'
  },
  title: {
    flexGrow: 1
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0
    }
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9)
    }
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
    paddingTop: '0px',
    paddingLeft: '0px',
    backgroundColor: '#f1f3f5'
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4)
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column'
  },
  fixedHeight: {
    height: 240
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('sm')]: {
      display: 'none'
    }
  },
  mainlistItem: {
    backgroundColor: '#fff',
    color: '#000',
    textDecoration: 'none',
    '&:hover': {
      backgroundColor: '#cccccc'
    },
    '&:active': {
      backgroundColor: '#dddddd'
    }
  },
  menutab: {
    root: {
      textTransform: 'none',
      color: '#fff',
      fontWeight: theme.typography.fontWeightRegular,
      fontSize: theme.typography.pxToRem(15),
      marginRight: theme.spacing(1),
      '&:focus': {
        opacity: 1
      }
    }
  }
}))

const menu = [
  {
    id: 0,
    label: '게시물 목록',
    path: '/'
  },
  {
    id: 1,
    label: '소개 입니다',
    path: '/page-me/'
  },
  {
    id: 2,
    label: 'TAG',
    path: '/page-tag/'
  }
]
const mainLayout: React.FC = ({ children }) => {
  const data = useStaticQuery(mainQuery)
  const classes = useStyles()
  const theme = useTheme()
  const [value, setValue] = React.useState(0)

  // console.log(window.location)
  // tab을 위한 작업
  const handleChange = (event: any, newValue: number) => {
    let tabidx = menu.filter(item => item.id == newValue)
    navigate(tabidx[0].path)
  }
  const [mobileOpen, setMobileOpen] = React.useState(false)

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen)
  }
  // tab이 눌려져있는 효과를 표현하기 위한 memohook
  if (typeof window !== `undefined`) {
    React.useMemo(() => {
      if (location.pathname.split('/')[1] != '') {
        let tabidx = menu.filter(item => item.path == '/' + location.pathname.split('/')[1] + '/')
        if (tabidx.length > 0) setValue(tabidx[0].id)
      } else {
        //navigate('/')
        setValue(0)
      }
    }, [value])
  }
  const drawer = (
    <div>
      <div className={classes.toolbar} />
      <Divider />
      <div>
        <Link className={classes.mainLogo} to="/">
          <Typography component="h1" variant="h5" color="inherit" noWrap className={classes.title}>
            {data.site.siteMetadata.title}
          </Typography>
        </Link>
        <List>
          <div>
            <Divider />
            <Link to={'/'} className={classes.mainlistItem}>
              <ListItem selected={value === 0} button>
                <ListItemText primary="게시글 목록" />
              </ListItem>
            </Link>
            <Divider />
            <Link to={'/page-me/'} className={classes.mainlistItem}>
              <ListItem selected={value === 1} button>
                <ListItemText primary="소개" />
              </ListItem>
            </Link>
            <Link to={'/page-tag/'} className={classes.mainlistItem}>
              <ListItem selected={value === 2} button>
                <ListItemText primary="태그" />
              </ListItem>
            </Link>
            <Divider />
          </div>
        </List>
      </div>
    </div>
  )
  return (
    // <div className={classes.root}>
    //   <CssBaseline />

    //   <AppBar position="fixed" className={classes.appBar}>
    //     <Toolbar>
    //       <IconButton color="inherit" aria-label="open drawer" edge="start" onClick={handleDrawerToggle} className={classes.menuButton}>
    //         <MenuIcon />
    //       </IconButton>
    //       <Link style={{ textDecoration: 'none' }} to="/">
    //         <Typography variant="h6" noWrap>
    //           {data.site.siteMetadata.title}
    //         </Typography>
    //       </Link>
    //     </Toolbar>

    //     {/* <Tabs variant="fullWidth" value={value} onChange={handleChange} centered indicatorColor="primary">
    //       {menu.map(item => (
    //         <Tab className={classes.menutab} key={item.id} label={item.label} />
    //       ))}
    //     </Tabs> */}
    //   </AppBar>

    //   <nav className={classes.drawer} aria-label="mailbox folders">
    //     {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
    //     {/* 작아졌을때 Drawer */}
    //     <Hidden smUp implementation="css">
    //       <Drawer
    //         variant="temporary"
    //         anchor={theme.direction === 'rtl' ? 'right' : 'left'}
    //         open={mobileOpen}
    //         onClose={handleDrawerToggle}
    //         classes={{
    //           paper: classes.drawerPaper
    //         }}
    //         ModalProps={{
    //           keepMounted: true // Better open performance on mobile.
    //         }}
    //       >
    //         {drawer}
    //       </Drawer>
    //     </Hidden>
    //     <Hidden xsDown implementation="css">
    //       <Drawer
    //         classes={{
    //           paper: classes.drawerPaper
    //         }}
    //         variant="permanent"
    //         open
    //       >
    //         {drawer}
    //       </Drawer>
    //     </Hidden>
    //   </nav>
    //   <main className={classes.content}>
    //     <div className={classes.toolbar} />
    //     {children}
    //   </main>
    // </div>
    // <div className={classes.root}>
    //   <CssBaseline />

    //   <nav aria-label="mailbox folders">
    //     {/* The implementation can be swapped with js to avoid SEO duplication of links. */}

    //     <Hidden smDown>
    //       <div>
    //         <Link className={classes.mainLogo} to="/">
    //           <Typography component="h1" variant="h5" color="inherit" noWrap className={classes.title}>
    //             {data.site.siteMetadata.title}
    //           </Typography>
    //         </Link>
    //         <List>
    //           <div>
    //             <Divider />
    //             <Link to={'/'} className={classes.mainlistItem}>
    //               <ListItem selected={value === 0} button>
    //                 <ListItemText primary="게시글 목록" />
    //               </ListItem>
    //             </Link>
    //             <Divider />
    //             <Link to={'/page-me/'} className={classes.mainlistItem}>
    //               <ListItem selected={value === 1} button>
    //                 <ListItemText primary="소개" />
    //               </ListItem>
    //             </Link>
    //             <Link to={'/page-tag/'} className={classes.mainlistItem}>
    //               <ListItem selected={value === 2} button>
    //                 <ListItemText primary="태그" />
    //               </ListItem>
    //             </Link>
    //             <Divider />
    //           </div>
    //         </List>
    //       </div>
    //     </Hidden>
    //   </nav>
    //   <main className={classes.content}>
    //     <div className={classes.toolbar} />
    //     <Hidden mdUp>
    //       <div className={classes.appBar}>
    //         <div className={classes.appBarHeader}>
    //           <IconButton style={{ float: 'left' }} aria-label="delete">
    //             <MenuIcon />
    //           </IconButton>
    //           <IconButton style={{ float: 'right' }} aria-label="delete">
    //             <MenuIcon />
    //           </IconButton>

    //           <Typography variant="h4">{data.site.siteMetadata.title}</Typography>
    //         </div>

    //         <Tabs variant="fullWidth" value={value} onChange={handleChange} centered indicatorColor="primary">
    //           {menu.map(item => (
    //             <Tab className={classes.menutab} key={item.id} label={item.label} />
    //           ))}
    //         </Tabs>
    //       </div>
    //       <Divider></Divider>
    //     </Hidden>
    //     {children}
    //   </main>
    // </div>

    <div className={classes.root}>
      <Helmet
        title={data.site.siteMetadata.title}
        meta={[
          { name: 'description', content: data.site.siteMetadata.description },
          { name: 'keywords', content: data.site.siteMetadata.keywords }
        ]}
      />

      <Hidden smDown>
        <Drawer
          variant="permanent"
          classes={{
            paper: clsx(classes.drawerPaper)
          }}
          open={true}
        >
          <div>
            <Link className={classes.mainLogo} to="/">
              <Typography component="h1" variant="h5" color="inherit" noWrap className={classes.title}>
                {data.site.siteMetadata.title}
              </Typography>
            </Link>
            <List>
              <div>
                <Divider />
                <Link to={'/'} className={classes.mainlistItem}>
                  <ListItem selected={value === 0} button>
                    <ListItemText primary="게시글 목록" />
                  </ListItem>
                </Link>
                <Divider />
                <Link to={'/page-me/'} className={classes.mainlistItem}>
                  <ListItem selected={value === 1} button>
                    <ListItemText primary="소개" />
                  </ListItem>
                </Link>
                <Link to={'/page-tag/'} className={classes.mainlistItem}>
                  <ListItem selected={value === 2} button>
                    <ListItemText primary="태그" />
                  </ListItem>
                </Link>
                <Divider />
              </div>
            </List>
          </div>
        </Drawer>
      </Hidden>

      <main className={classes.content}>
        <Hidden mdUp>
          <div className={classes.appBar}>
            <div className={classes.appBarHeader}>
              <IconButton style={{ float: 'left' }} aria-label="delete">
                <MenuIcon />
              </IconButton>
              <IconButton style={{ float: 'right' }} aria-label="delete">
                <MenuIcon />
              </IconButton>

              <Typography variant="h4">{data.site.siteMetadata.title}</Typography>
            </div>

            <Tabs variant="fullWidth" value={value} onChange={handleChange} centered indicatorColor="primary">
              {menu.map(item => (
                <Tab className={classes.menutab} key={item.id} label={item.label} />
              ))}
            </Tabs>
          </div>
          <Divider></Divider>
        </Hidden>
        <div>{children}</div>
      </main>
    </div>
  )
}

export default mainLayout
